import React, { PureComponent } from "react";
import { getImageSize } from 'react-image-size';
import ReactPlayer from "react-player"
import { POPUP_VIDEO_RENEW } from '../../../../actions/themeActions'
import { isTagShop } from "../../../../actions/api";
import { reelPlayTimeUpdateData } from "../../../../utils";
const mediaReplaceText = (mediaUrl, networkId) => {
    switch (networkId) {
        case 25:
            const soundCloud = String(mediaUrl).replace("https://w.soundcloud.com/player/?url=", "")
            return soundCloud
            break;

        default:
            return mediaUrl
            break;
    }

}
export default class ReelVideo extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            loadError: false,
            paddingBottom: 100,
            videoLoaded: false,
            imgUrl: this.props.data.postFileNew,
            played: 0
        }
    }
    componentWillMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }

        if (this.props.size) {
            this.setState({
                paddingBottom: this.props.size
            })
        } else {
            this.onMediaLoad();
        }
    }

    componentDidMount() {
        const { data } = this.props;
        let updateVideo = document.querySelector(`#video_p_${data.id}`);
        if (updateVideo) {
            updateVideo.play()
            updateVideo.muted = true;
        }

        if (this.props.size) {
            this.setState({
                paddingBottom: this.props.size
            })
        } else {
            this.onMediaLoad();
        }
    }

    onMediaLoad = async (props) => {
        const { data } = this.props;
        if (data && Object.keys(data).length > 0 && [4, 5, 3].includes(data.type)) {
            if (data.mediaHeight && data.mediaHeight != "" && data.mediaWidth && data.mediaWidth != "") {
                this.setState({
                    paddingBottom: (data.mediaHeight * 100) / data.mediaWidth
                })
            } else {
                try {
                    const { width, height } = await getImageSize(data.postFileNew);
                    this.setState({
                        paddingBottom: (height * 100) / width
                    })
                } catch (error) {
                    this.setState({
                        paddingBottom: 100
                    })
                }
            }
        }
    }
    setVideoLoaded = event => {
        this.setState({
            videoLoaded: event
        })
    }
    handleProgress = e => {


    }
    componentWillUnmount() {
        const { played } = this.state
        if (isTagShop) reelPlayTimeUpdateData(played)
    }
    onUpdateTime = (progress) => {
        try {
            if (isTagShop) {
                const { played } = this.state
                this.setState({
                    played: played + progress.playedSeconds
                })
            }
        }
        catch (e) {
            console.log(e)
        }

    }
    render() {
        const { VideoClass, data, wallID, controls, autoPlay, handleVideoEnded, muted } = this.props
        const { videoLoaded } = this.state
        const videoStyle = {
            paddingBottom: `100vh`,
        };
        return <div className={`${VideoClass}_wrap_ mediaHolder${data.id}`} style={videoStyle}>

            <ReactPlayer
                // ref={videoRef}
                className={`${VideoClass} video${data.id}`}
                url={mediaReplaceText(data.mediaUrl, data.network.id)}
                data-type="video"
                data-network={data.network.id}
                data-link={data.link}
                data-wall-id={wallID}
                data-item-id={data.id}
                data-feed-id={data.feedId ? data.feedId : ""}
                data-load={0}
                config={{
                    facebook: {
                        appId: '117029975354072',
                        attributes: {
                            'data-width': 'auto',
                            'data-height': `${document.querySelector('.ts_rt_post_in') ? `${document.querySelector('.ts_rt_post_in').clientHeight} px` : '100%'}`
                        }
                    },
                    file: {
                        forceVideo: true,
                        attributes: {
                            muted,
                            // onError: (e) => IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP_VIDEO(e)
                        }
                    }
                }}
                loop={true}
                onError={(e) => {
                    if (!data.stories || data.stories == 0) POPUP_VIDEO_RENEW(e, data.id)

                    //this.setVideoLoaded(false)
                }}
                muted={muted}
                volume={autoPlay ? 1 : 0}
                playsinline={true}
                onReady={(e) => { this.setVideoLoaded(true) }}
                playing={autoPlay}
                height={'100%'}
                width={'100%'}
                controls={controls}
                onProgress={this.onUpdateTime}
                // onProgress={onPlayingVideo}
                style={{ backgroundImage: (!videoLoaded) ? `url(${data.postFileNew})` : null }}
                onEnded={handleVideoEnded ? handleVideoEnded : null}

            />
            {!videoLoaded ? <div className='tb_rpp_v_spinner'><div></div></div> : null}
        </div>
    }
}



